@import url('https://fonts.googleapis.com/css2?family=Ubuntu:wght@300;400&display=swap');

:root {
    --background-color: #282626;
    --dark: #1c1b1b;
    --offwhite: #f5f5f5;
    --red: #ff3535;
}


body {
    font-family: 'Ubuntu', sans-serif;
    background-color: var(--background-color);
    color: var(--offwhite);
    font-weight: 300;
}


